.Cards{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row;
        flex-flow: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    text-decoration: none;

}

.cardName{
    text-decoration: none;
    color: black;
}
.cardTittle{
    color: black;
    text-decoration: none;
}

.box {
    background-repeat: no-repeat;
    background-position: -500px -120px, 0 0;
    background-color: #ee9a62;
    background-image: -webkit-linear-gradient(
      top left,
      rgba(255, 255, 255, 0.2) 0%,
      rgba(255, 255, 255, 0.2) 37%,
      rgba(255, 255, 255, 0.8) 45%,
      rgba(255, 255, 255, 0.0) 50%
    );    
    background-image: -o-linear-gradient(
      0 0,
      rgba(255, 255, 255, 0.2) 0%,
      rgba(255, 255, 255, 0.2) 37%,
      rgba(255, 255, 255, 0.8) 45%,
      rgba(255, 255, 255, 0.0) 50%
    );
    background-image: -webkit-gradient(
      linear,
      left top, left bottom,
      color-stop(0, 0),
      color-stop(0%, rgba(255, 255, 255, 0.2)),
      color-stop(37%, rgba(255, 255, 255, 0.2)),
      color-stop(45%, rgba(255, 255, 255, 0.8)),
      color-stop(50%, rgba(255, 255, 255, 0.0))
    );
    background-image: -webkit-linear-gradient(
      0 0,
      rgba(255, 255, 255, 0.2) 0%,
      rgba(255, 255, 255, 0.2) 37%,
      rgba(255, 255, 255, 0.8) 45%,
      rgba(255, 255, 255, 0.0) 50%
    );
    background-image: linear-gradient(
      0 0,
      rgba(255, 255, 255, 0.2) 0%,
      rgba(255, 255, 255, 0.2) 37%,
      rgba(255, 255, 255, 0.8) 45%,
      rgba(255, 255, 255, 0.0) 50%
    );
    background-size: 250% 250%, 100% 100%;
    
    -webkit-transition: background-position 0s ease;       
         -o-transition: background-position 0s ease;
            transition: background-position 0s ease;
  }
  
  .box:hover {
    background-position: 0 0, 0 0;
    
    -webkit-transition-duration: 0.5s;
            -o-transition-duration: 0.5s;
       transition-duration: 0.5s;
  }
  a.box {
    margin-left: 10px;
    margin-right: 10px;
    width: 400px;
}


p.cardTittle {
  height: 50px;
}

a.box {
  background-color: #ffffff;
  color: #000000;
  text-decoration: none;
  -webkit-box-shadow: 2px 2px 2px 1px #888888;
          box-shadow: 2px 2px 2px 1px #888888;
  line-height: 50px;
}

p.cardName {
  background-color: #45daff;
  color: #000000;
  border-color: #000000;
  height: 50px;
  margin: 0;
}




